.MuiInputBase-root {
    border-radius: 1rem !important;
}

.MuiInputBase-root:focus {
    border-radius: 1rem !important;
    border: 6px solid rgb(46, 224, 210) !important;
}

.MuiOutlinedInput-input{
    padding: 15px 14px;
}

.MuiInputLabel-outlined {
    z-index: 1;
    -webkit-transform: translate(19px, 20px) scale(1) !important;
            transform: translate(19px, 20px) scale(1) !important;
    pointer-events: none;
}

.MuiInputLabel-outlined.MuiInputLabel-shrink{
    -webkit-transform:translateY(7px) scale(0.75) !important;
            transform:translateY(7px) scale(0.75) !important;
    margin-left: 13px;
    /* background-color: blue; */
}

.MuiFormHelperText-root.Mui-error {
    color: #f44336;
    margin-bottom: 10px;
}

