.intl-tel-input{
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 1rem;
    padding: 0.8rem 0px;
}

.intl-tel-input input{
    border: none;
    outline: none;
}

.phone__input{
    width: 100%;
    margin-bottom: 1rem;
    border-radius:1rem;
}

/* .phone__input:focus{
    border: 3px solid cyan !important;
} */

.phone__input.focus{
    margin-bottom: 0.5rem;
}
.react-tel-input{
    width: 100%;
    /* border: 1px solid #ccc; */
    padding: 0.6rem 0px;
    border-radius: 1rem;
    box-shadow: 0 0 7pt 0.5pt #d3d3d3;
}

.focus div.react-tel-input {
    border: 1px solid #f44336;
}

.phone__input:focus{
    border:5px solid black !important;
    /* background-color:red; */
}

.react-tel-input input.form-control{
    border: none;
    outline: none;
    width: 100%;
    font-size: 1rem;
    height: 100%;
}

.react-tel-input input.form-control::-webkit-input-placeholder{
    font-size:1rem;
}

.react-tel-input input.form-control:-ms-input-placeholder{
    font-size:1rem;
}

.react-tel-input input.form-control::placeholder{
    font-size:1rem;
}

.react-tel-input input.form-control:focus{
    border: none;
    outline: none;
}

.react-tel-input input.form-control:hover, .react-tel-input input.form-control:active{
    border: none;
    outline: none;
}


.react-tel-input .flag-dropdown {
    border-radius: 1rem 0 0 1rem !important;
}

/* .react-tel-input .flag-dropdown:hover{
    border: 1px solid red !important;
    border-radius: 1rem 0 0 1rem !important;
} */


.MuiOutlinedInput-input {
    padding: 13.5px 18px !important;
}

.react-tel-input{
    padding: 0.4rem 0px;
}

.MuiInputLabel-outlined.MuiInputLabel-shrink {
    -webkit-transform: translateY(3px) scale(0.75) !important;
            transform: translateY(3px) scale(0.75) !important;
    margin-left: 18px;
    /* background-color: blue; */
}

.MuiInputLabel-formControl {
    top: -6px !important;
    left: 0;
    position: absolute;
    -webkit-transform: translate(0, 24px) scale(1);
            transform: translate(0, 24px) scale(1);
}


.MuiOutlinedInput-notchedOutline {
    border-color: transparent !important;   
    box-shadow: 0 0 7pt 0.5pt #d3d3d3;
}

.MuiOutlinedInput-root.Mui-error .MuiOutlinedInput-notchedOutline {
    border-color: #f44336 !important;
}

.MuiOutlinedInput-notchedOutline:focus{
    border: 10px solid rgb(59, 164, 190) !important;
    outline:none !important;
}
