    background-position: center;
    background-size: cover;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    scrollbar-width: none;
}
.landing-navbar
{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 14%;
    position: fixed;
    background: rgba(255,255,255, 0.4);
    padding-right: 10%;
    z-index: 15;
    padding-left: 2%;
}

.landing-navbar a
{
    color: black;
    text-decoration: none;
}

.landing-navbar h1
{
    cursor: pointer;
}

.links
{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 22%;
    font-weight: 300;
}

#signup-button
{
    padding:0.2rem 1rem;
    border-radius: 16px;
    background: linear-gradient(to bottom right, rgba(0,0,0,0.2), white);
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.5);
    transition: 0.2s;
}

#signup-button:hover
{
    background: linear-gradient(to bottom right, rgba(0,0,0,0.2), white);
    box-shadow: inset 2px 2px 8px rgba(0, 0, 0, 0.15)
}

.hamburger-menu
{
    display: none;
}

@media (max-width: 850px)
{

    .landing-navbar
    {
        padding: 2% 5%;
        z-index: 10;
    }

    .landing-navbar h1
    {
        cursor: pointer;
    }

    .links
    {
        display: none;
    }

    .hamburger-menu
    {
        display: flex;
        flex-direction: column;
        width: 35%;
        height: 100%; 
        transition: 0.3s;
    }

    .hamburger-menu i {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        width: 100%;
    }

    .nav-links
    {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: auto;
        background-color: snow;
        box-shadow: 0 0 18px 0 black;
        border-radius: 10px;
        padding: 0 6%;
        width: 100%;
        z-index: 11;
    }

    #close-menu
    {
        width: 100%;
        height: 20%;
        display: flex;
        margin: 4%;
        justify-content: flex-end;
        align-items: center;
    }

    .nav-links a
    {
        text-decoration: none;
        color: black;
        width: 100%;
        height: 100%;
    }

    .nav-links div
    {
        width: 100%;
        border-radius: 10px;
        text-align: center;
        margin-bottom: 1rem;
        padding: 1rem;
        left: 2px;
        transition: 0.3s;
        cursor: pointer;
    }

    .nav-links div:active
    {
        background-color: #98a4cc;
        color: snow;
    }

    #blog-link:active
    {
        background-color: #98a4cc;
        color: snow;
    }
}
.waiting{
    position: relative;
}

.waiting__page{
    width: 100vw;
    display: flex;
    justify-content: center;
    height: 100vh;
}

.waiting__list{
    flex: 50% 1;
    height: 100%;
    display:flex;
    justify-content: center;
    align-items: center;
}


.waiting__list__text{
    width: 80%;
    /* border: 1px solid #ccc; */
    /* box-shadow: 0px 0px 1rem  rgba(0,0,0,0.5); */
    border-radius:1rem;
    padding:2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
}

.waiting__list__text h4{
    font-weight: 600;
    font-size: 30px;
}

.waiting__list__text h5{
    font-size: 25px;
}

.waiting__list__text img{
    height:50px;
    width:50px;
}

.waiting__image{
    flex: 50% 1;
    display:flex;
    height: inherit;
}

.waiting__list__image{
    height: 100%;
    width: 100%;
    background-image: url(https://skyhype.in/skyhype/Images/Web%201920%20%E2%80%93%203@2x_adobespark.png);
    background-size: cover;
    background-position: center;
}


@media screen and (max-width:1000px){
    .waiting .logo{
        /* top: 1rem; */
        color: black;
        left: 3rem;
    }

    .waiting__list__text{
        padding: 1rem;
    }

    .waiting__list__text h5{
        font-size: 23px;
    }


}


@media screen and (max-width:850px){
    .waiting__page{
        flex-direction: column-reverse;
    }
    
    .waiting__list{
        align-items: start;
    }
    .waiting__list__text h4{
        font-size: 25px;
    }

    .waiting__list__text h5{
        font-size: 20px;
    }
}



@media screen and (max-width:750px){
    .waiting__list__text{
        width: 90%;
    }

    .waiting__image{
        flex: 80% 1;
        display:flex;
        height: inherit;
    }

}
